import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import avatar from '../img/avatar_.png'


class Resume extends Component {
    render() {
        return(
            <div>
                <Grid>
                    <Cell col={4}>
                        <div style={{textAlign: 'center'}}>
                        <img 
                            alt="avatar" 
                            className="avatar-img" 
                            src={avatar} 
                        />

                        </div>

                        <h2 style={{paddingTop: '2em'}}>Flavio Cruz</h2>
                        <h4 style={{color: 'gray'}}>Programmer</h4>
                        <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                        <p>
                        O Lorem Ipsum é um texto modelo da indústria tipográfica e de impressão. 
                        O Lorem Ipsum tem vindo a ser o texto padrão usado por estas indústrias desde o ano de 1500, 
                        quando uma misturou os caracteres de um texto para criar um espécime de livro.
                        </p>
                        <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                        <h5>Address</h5>
                        <p>Praceta Antonio Sergio, 16 1E / 3830-104 Ilhavo / Portugal</p>
                        <h5>Phone</h5>
                        <p>(351) 936 458 996</p>
                        <h5>E-mail</h5>
                        <p>flcable@gmail.com</p>
                        <h5>Web</h5>
                        <p>mywebsite.com</p>
                    </Cell>
                    <Cell className="resume-right-col" col={8}>Right Side</Cell>
                </Grid>
            </div>
        )
    }
}

export default Resume;